.container{
    display: block;
}
.about{
    display: flex;
    background-color:white;
    align-items: center;
    justify-content: center;
    padding: 0 50px;
    column-gap: 25px;
}
.activities_img{
    width: 500px;
    height: 360px;
    border-radius: 50px;
    margin: 100px 0;
}
.description{
    width: 50%;
    
}
.description h1{
    color: black;
}
.description p{
    color: black;
}
.button_read{
    display: inline-block;
    padding: 10px 55px;
    background-color:rgba(71, 174, 221, 0.781);
    color: black;
    border-radius: 5px;
    transition: all 0.3s;
    border: 1px solid #ffffff;
    font-size: 1rem;
    margin-top: 15px;
}
.button_read:hover{
    display: inline-block;
    padding: 10px 55px;
    background-color: #ffffff;
    color: black;
    border-radius: 5px;
    transition: all 0.3s;
    border: 1px solid rgba(71, 174, 221, 0.781);
    font-size: 1rem;
    cursor: pointer;
    background-color:white;
}
.doctors{
    display: flex;
    background-color:rgb(71, 174, 221);
    flex-direction: column;
    padding: 0 50px;
    column-gap: 25px;
    overflow: hidden;
}
.doctors h1{
    margin-top: 25px;
    text-align: center;
}
.doctors p{
    text-align: center;
}

.doctors_cards{
    margin: 25px 0;
    display: flex;
    column-gap: 25px;
    row-gap: 25px;
}



@media(max-width:915px){
    .container{
        display: flex;
        align-items: center;
        justify-content: flex-start;
        flex-direction: column;
        column-gap: 50px;
        padding: 0 0px;
        overflow: hidden;
    }
    .about{
        display: flex;
        background-color:white;
        flex-direction: column-reverse;
        padding: 0 10px;
        column-gap: 25px;
    }
    .description{
        width: 100%;
        margin-top: 25px;
    }
    .activities_img{
        width: 90%;
        height: 40%;
        border-radius: 50px;
        margin-top: 25px;
        margin-bottom: 25px;
    }
    .doctors{
        display: flex;
        background-color:rgb(71, 174, 221);
        flex-direction: column;
        padding: 0 10px;
        column-gap: 25px;
        width: 100%;
    }
    .doctors h1{
        text-align: center;
    }
    
    .doctors_cards{
        margin: 25px 0;
        display: flex;
        row-gap: 25px;
    }
}