.container{
    position: relative;
    padding: 0 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 25px 0;

}
h2{
    color: black;
    padding-top: 25px;
}
.title{
    display: flex;
    padding: 25px 0;
    column-gap: 25px;
}
.donate_page{
    display: flex;
    flex-direction: column;
    width: 400px;
    padding: 15px 15px;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
    margin-top: 50px;
    margin-bottom: 50px;
    border-radius: 15px;
    background-color:rgba(71, 174, 221, 0.205);
    transition: .3s all;
    z-index: 98;
}
.donate_page:hover{
    scale: 1.3;
    transition: .3s all;
    z-index: 98;
    background-color:rgb(71, 174, 221);
}
.donate_page h4{
    margin-bottom: 10px;
}
.donate_page:hover h4{
    margin-bottom: 10px;
    color: white;
}

@media(max-width:915px){
    .container{
        position: relative;
        padding: 0 10px;
        display: flex;
        padding: 0 10px;
        text-align: center;
    
    }
    h2{
        color: black;
    }
    .donate_page{
        display: flex;
        flex-direction: column;
        width: 100%;
        padding: 15px 15px;
        box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
        margin-top: 0px;
        margin-bottom: 25px;
        border-radius: 15px;
        background-color:rgba(71, 174, 221, 0.205);
    }
    .title{
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 25px 0;
        column-gap: 25px;
    }
    .title img{
        width:90%;
    }
    .donate_page:hover{
        scale: 1;
        transition: .3s all;
        z-index: 99;
        background-color:rgb(71, 174, 221);
    }
}


