.card{
    width: 250px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;  
    transition: all 0.3s; 
    background-color:rgba(71, 174, 221, 0.205);
    border-radius: 15px;
    padding: 15px 15px;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}
.card:hover{
    scale: 1.1;  
    transition: all 0.3s;
    cursor: default;
}
.card:hover .card_img{
    background-color: rgb(184, 170, 124);
    transition: all 0.3s;
}
.card .card_img{
    margin-top: 15px;
    background-color:rgba(71, 174, 221, 0.781);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100px;
    height: 100px;
    border-radius: 50px;
}
.card .card_img img{
    width: 70px;
    height: 70px;
}
.card h1{
    font-size: 2rem;
    margin-top: 15px;
    color: black;
}

.card p{
    color: black;
}


@media(max-width:915px){
    .card{
        width: 100%;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        text-align: center;  
        transition: all 0.3s; 
        background-color:rgba(71, 174, 221, 0.205);
        border-radius: 15px;
        padding: 15px 15px;
    }
    .card:hover{
        scale: 1;  
        transition: all 0.3s;
    }
}