.doctor_card{
    position: relative;
    width: 350px;
    border-radius: 25px;
    background-color: white;
    text-align: center;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
    overflow: hidden;
}
.doctor_card img{
    width: 100%;
    border-radius: 25px 25px 0 0;
    transition: .3s all;
}
.doctor_card:hover img{
    width: 100%;
    border-radius: 25px 25px 0 0;
    scale: 1.1;
    transition: .3s all;
}
.doctor_card h2{
    color: black;
    font-size: 2rem;
}
.doctor_card p{
    color: black;
    padding-bottom: 10px;
}
.desc{
    position: absolute;
    bottom:85px;
    background-color: white;
    opacity: 0;
    transition: .3s all;
}
.doctor_card:hover {
    cursor: pointer;
} 
.doctor_card:hover .desc{
    opacity: 1;
    transition: .3s all;
} 

@media(max-width:915px){
    .doctor_card{
        position: relative;
        width: 280px;
        border-radius: 25px;
        background-color: white;
        text-align: center;
        box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
    }
}